import React, { useReducer, useCallback, useEffect } from "react";
import { connect } from "react-redux";
import { Button, Input } from "../../shared/components/FormElements";
import ArchedHeader from "../../shared/components/UIElements/ArchedHeader";
import AccountService from "../../shared/services/account-service";
import Toasteo from "toasteo";
import "./Inscription.css";
import { ReactComponent as EyeVisibility } from "./Eye.svg";
import PhoneOption from "../components/PhoneOption";
import {
  VALIDATOR_PHONE,
  VALIDATOR_REQUIRE,
  VALIDATOR_MINLENGTH,
  VALIDATOR_MAXLENGTH,
} from "../../shared/services/validators";
import { getWilayas, getCommunesByWilayaId } from "../../shared/services/util";
import { setClientData } from "../../store/actions";
import LoadingSpinner from "../../shared/components/UIElements/LoadingSpinner";

const formReducer = (state, action) => {
  switch (action.type) {
    case "INPUT_CHANGE":
      let formIsValid = true;
      for (const inputId in state.inputs) {
        if (inputId === action.inputId) {
          formIsValid = formIsValid && action.isValid;
        } else {
          formIsValid = formIsValid && state.inputs[inputId].isValid;
        }
      }
      return {
        ...state,
        inputs: {
          ...state.inputs,
          [action.inputId]: {
            ...state.inputs[action.inputId],
            value: action.value,
            isValid: action.isValid,
          },
        },
        formIsValid,
        isSubmitted: false,
      };

    case "FETCH_WILAYAS":
      return {
        ...state,
        wilayas: action.wilayas,
      };

    case "FETCH_COMMUNES":
      return {
        ...state,
        communes: action.communes,
      };
    case "SUBMIT":
      return {
        ...state,
        isSubmitted: true,
      };
    case "IS_SUBMITTING":
      return {
        ...state,
        isSubmitting: action.isSubmitting,
      };
    case "togglePasswordVisibility":
      return {
        ...state,
        inputs: {
          ...state.inputs,
          password: {
            ...state.inputs.password,
            showPassword: !state.inputs.password.showPassword,
          },
        },
      };
    default:
      return state;
  }
};

const Inscription = (props) => {
  const [formState, dispatch] = useReducer(formReducer, {
    inputs: {
      username: {
        value: "",
        isValid: false,
      },
      password: {
        value: "",
        isValid: false,
        showPassword: false,
      },
      wilaya: {
        value: "",
        isValid: false,
      },
      commune: {
        value: "",
        isValid: false,
      },
      phone: {
        value: "",
        isValid: false,
      },
    },
    formIsValid: false,
    isSubmitted: false,
    wilayas: [],
    communes: [],
  });

  useEffect(() => {
    let wilayas;
    const fetchWilayas = async () => {
      wilayas = await getWilayas();
      dispatch({ type: "FETCH_WILAYAS", wilayas });
    };
    fetchWilayas();
  }, []);

  const changeInputHandler = useCallback(async (id, value, isValid) => {
    if (id === "wilaya") {
      let communes = await getCommunesByWilayaId(value);
      dispatch({ type: "FETCH_COMMUNES", communes });
      value = value.split("-")[1];
    }

    dispatch({
      type: "INPUT_CHANGE",
      inputId: id,
      value: value,
      isValid: isValid,
    });
  }, []);

  const onSubmit = (e) => {
    e.preventDefault();
    if (!formState.formIsValid) {
      dispatch({
        type: "SUBMIT",
      });
      return;
    }

    const data = Object.assign(
      {},
      ...Object.keys(formState.inputs).map((key) => ({
        [key]: formState.inputs[key].value,
      }))
    );

    dispatch({
      type: "IS_SUBMITTING",
      isSubmitting: true,
    });
    window.Toasteo = new Toasteo({});

    AccountService.registerCient(data).then(
      (res) => {
        console.log(res);
        window.Toasteo.success("Inscription Russie !");
        console.log("Sucesss");
        dispatch({
          type: "IS_SUBMITTING",
          isSubmitting: false,
        });
        // Set Redux state and localstorage entries
        props.confirmSignUp({
          client: res.createdClient,
          token: res.token,
        });
        // Redirect user to Dashboard (new abonnement in real)
        props.history.push("/abonnement/new");
        console.log(props);
      },
      (err) => {
        console.error(err);
        window.Toasteo.error(err.message || "echc d'inscription");
        dispatch({
          type: "IS_SUBMITTING",
          isSubmitting: false,
        });
      }
    );
  };

  return (
    <div className="inscription">
      {formState.isSubmitting && <LoadingSpinner />}
      <ArchedHeader />

      <div className="inscription__content">
        <p className="inscription__title">
          Crer un compte
          {formState.isSubmitted && !formState.formIsValid && (
            <span
              style={{
                fontSize: "80%",
                fontWeight: "500",
                color: "red",
                display: "block",
              }}
            >
              Veuillez remplir le formulaire avec des donnes valides!
            </span>
          )}
        </p>
        <form className="inscription__form" onSubmit={onSubmit}>
          <Input
            element="input"
            id="username"
            type="text"
            validators={[
              VALIDATOR_REQUIRE(),
              VALIDATOR_MINLENGTH(3),
              VALIDATOR_MAXLENGTH(24),
            ]}
            placeholder="Votre nom de compte (requis)"
            errorText="Veuilez entrer un nom de compte de 3  24 charactres!"
            onInput={changeInputHandler}
          />
          <div className="inscription__password">
            <Input
              element="input"
              id="password"
              type={
                formState.inputs.password.showPassword ? "text" : "password"
              }
              icon={EyeVisibility}
              onIconClick={() => dispatch({ type: "togglePasswordVisibility" })}
              iconClassName={
                formState.inputs.password.showPassword ? "active" : ""
              }
              validators={[
                VALIDATOR_REQUIRE(),
                VALIDATOR_MINLENGTH(6),
                VALIDATOR_MAXLENGTH(24),
              ]}
              errorText="Veuillez entrer un mot de passe de 6  24 charactres."
              placeholder="Votre mot de passe (requis)"
              onInput={changeInputHandler}
            />
          </div>
          <Input
            element="select"
            id="wilaya"
            options={formState.wilayas}
            validators={[VALIDATOR_REQUIRE()]}
            errorText="Veuillez slectionner votre wilaya."
            placeholder="Votre Wilaya (requis)"
            onInput={changeInputHandler}
          />
          <Input
            element="select"
            id="commune"
            validators={[VALIDATOR_REQUIRE()]}
            options={formState.communes}
            errorText={
              formState.inputs.wilaya.value === ""
                ? "Veuillez slectionner votre wilaya d'abord puis votre communes."
                : "Veuillez slectionner votre commune."
            }
            placeholder="Votre Commune (requis)"
            onInput={changeInputHandler}
          />
          <Input
            element="input"
            id="phone"
            type="text"
            required={false}
            validators={[VALIDATOR_PHONE()]}
            helpText="Mettre un numro de tlphone nous aidera plus  tre en contact avec vous, et vous prvenir et aider en cas de problme. Vous serez parmi les premiers au courant de nos dernire nouveaut et surprises, ainsi que bnficierait des bonus que nous rserverons"
            icon={PhoneOption}
            errorText="Veuillez entrer un numro de tlphone valide, ex: 0771000000"
            placeholder="Tlphone (optional), ex: 0771000000"
            onInput={changeInputHandler}
          />
          <Button type="submit" className="inscription__form-sinscrire">
            S'inscrire
          </Button>
        </form>

        <footer>
          <div className="inscription__footer-content">
            <p>Vous avez dj un compte?</p>
            <div className="inscription__footer-actions">
              <hr />
              <Button
                transparent
                type="link"
                to="/login"
                className="inscription__login"
              >
                Se connecter
              </Button>
              <hr />
            </div>
          </div>
        </footer>
      </div>
    </div>
  );
};

const mapStateToProps = (state) => {
  return {};
};

const mapDispatchToProps = (dispatch) => {
  return {
    confirmSignUp: (params) => {
      dispatch(setClientData(params));
    },
    // abonner: () => dispatch({ type: "ABONNEMENT" }),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Inscription);
